body {
	background: #fffcf9;
}

.cell {
	display: flex;
	justify-content: center;
	width: 100%;
	position: relative;
	background: #fffcf9;
	border: 1px solid #999;
	font-size: 24px;
	font-weight: bold;
	align-items: center;
	border-radius: 0;
}

.highlight {
	background: #ffd166;
}

.blue {
	color: #26547c;
}

.red {
	color: #ef476f;
}

.x {
	color: #26547c;
	border-color: #26547c !important;
}

.o {
	color: #ef476f;
	border-color: #ef476f !important;
}

.x-win {
	background-color: #26547c;
	border-color: #26547c !important;
	color: #fffcf9;
}

.o-win {
	background-color: #ef476f;
	border-color: #ef476f !important;
	color: #fffcf9;
}

.d {
	background: #18130a;
	border-color: #18130a;
	color: #fffcf9;
}

.cell:focus {
	border-color: #d50000;
	outline: none;
}

.container {
	display: flex;
	justify-content: center;
}

.center-all {
	align-items: center;
}

.full-height {
	height: 100%;
}

.game-wrapper {
	display: grid;
	margin-top: 1em;
	margin-bottom: 1em;
	font-size: 9px;
	grid-template-columns: 80vmin;
	overflow: hidden;
}

.big-board {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-gap: 1em;
	justify-content: center;
}

.small-board {
	display: grid;
	grid-template-columns: repeat(3, 1fr);
	grid-gap: 1px;
	justify-content: center;
}

.small-board button::before {
	content: '';
	padding-bottom: 100%;
	display: inline-block;
	vertical-align: top;
}

.game-info {
	display: flex;
	justify-content: center;
	align-items: center;
	font-size: 1.5em;
}

@media only screen and (min-width: 500px) {
	.game-wrapper {
		font-size: 12px;
	}
}

@media only screen and (min-width: 700px) {
	.game-wrapper {
		font-size: 36px;
	}
}

.btn {
	font-size: 14px;
	display: inline-block;
	height: 36px;
	min-width: 88px;
	padding: 6px 16px;
	line-height: 1.42857143;
	text-align: center;
	white-space: nowrap;
	vertical-align: middle;
	-ms-touch-action: manipulation;
	touch-action: manipulation;
	cursor: pointer;
	-webkit-user-select: none;
	-moz-user-select: none;
	-ms-user-select: none;
	user-select: none;
	border: 0;
	border-radius: 2px;
	background: #048966;
	color: #fffcf9;
	outline: 0;
}
.btn:focus {
	background: darken(#048966, 12%);
}

.btn-raised {
	box-shadow: 0 2px 2px 0 rgba(0, 0, 0, 0.14), 0 1px 5px 0 rgba(0, 0, 0, 0.12),
		0 3px 1px -2px rgba(0, 0, 0, 0.2);
	transition: box-shadow 0.28s cubic-bezier(0.4, 0, 0.2, 1);
}
.btn-raised:active {
	box-shadow: 0 8px 10px 1px rgba(0, 0, 0, 0.14),
		0 3px 14px 2px rgba(0, 0, 0, 0.12), 0 5px 5px -3px rgba(0, 0, 0, 0.4);
}

.hide {
	visibility: hidden;
}

.game-button {
	margin: 10px;
	display: flex;
	justify-content: center;
	align-items: center;
}
